<template>
<b-modal v-model="modalShow" title="Set Order Pairs">
	<b-row v-for="(item,index) in value.features" class="mb-2">
		<b-col>
			<b-form-select :options="options" :value="value.features[index].attributes.FirstOrderName" @input="update(index,'FirstOrderName', $event)"/>
		</b-col>
		<b-col>
			<b-form-select :options="options" :value="value.features[index].attributes.SecondOrderName" @input="update(index,'SecondOrderName', $event)"/>
		</b-col>
		<b-col>
			<b-form-input type="number" :value="value.features[index].attributes.MaxTransitTime" @input="update(index,'MaxTransitTime', $event)"/>
		</b-col>
	</b-row>
	<b-btn @click="add">Add Pairing</b-btn>
	<b-btn @click="remove":disabled="value.features.length == 0">Remove Paring</b-btn>
	<b-btn slot="modal-footer" block @click="$store.set('showOrderPair',false)" variant="primary">Ok</b-btn>
</b-modal>

</template>

<script>
import {sync, get} from 'vuex-pathify'

export default {
	computed: {
		orderLen: get('orderLen'),
		modalShow: sync('showOrderPair'),
		value: sync('optionalParam@order_pairs.value'),
		options: function() {
			var out = []
			for (var i = 1; i<=this.orderLen; i++) {
				out.push(this.$store.get(`requiredParam@Order.${i}.attributes.Name.value`))
			}
			return out
		}
	},
	methods: {
		add: function() {
			this.value = {features: [...this.value.features,
				{attributes:{
					FirstOrderName: "",
					SecondOrderName:"",
					MaxTransitTime: ""
				}}]}
		},
		remove: function() {
			this.value = {features: [...this.value.features.slice(0,-1)]}
		},
		update: function(index,type,event) {
			console.log(index,type,event)
			const copy = JSON.parse(JSON.stringify(this.value));
			copy.features[index].attributes[type] = event
			this.value = copy
		}
	}
}

</script>